"use strict";
var ListView = (function () {
    function ListView() {
        this.view = document.getElementById('view');
        this.products = document.getElementById('products');
    }
    ListView.prototype.render = function (list) {
        this.products.innerHTML = '';
        this.template = "\n\t\t<h1>" + list.name + "</h1>\n\t\tdas ist ein test\n\t\t<a href=\"#/lists/" + list.name + "/addProducts\">+ Produkt</a>\n\t\t<div id=\"toAdd\"></div>\n\t\t<div id=\"alreadyAdded\"></div>\n\t\t";
        this.view.innerHTML = this.template;
        this.updateList(list, 'toAdd');
        this.updateList(list, 'alreadyAdded');
    };
    ListView.prototype.updateList = function (list, type) {
        var listElement = document.getElementById(type);
        var product;
        var template = Object.keys(list[type]).map(function (id) {
            product = list[id];
            return "<li class=\"product\">\n\t\t\t\t<a href=\"#/lists/" + list + "/addlist/" + product.name + "\">\n\t\t\t\t\t" + product.name + "\n\t\t\t\t</a>\n\t\t\t</li>";
        }).join('');
        listElement.innerHTML = template;
    };
    return ListView;
}());
module.exports = ListView;



